import { Size } from "../types/Size"

// Implementing this interface implies that the width and/or height value can support "fr" values to consume free space in its parent.
/**
 * @public
 */
export interface WithFractionOfFreeSpace {
    /**
     * All free space in the parent, in px.
     * @internal
     */
    freeSpaceInParent: Size
    /**
     * The sum of all "fr" values in siblings wishing to consume free space. Each free space consuming child must divide its own "fr" value by this value.
     * @internal
     */
    freeSpaceUnitDivisor: Size
}

const key: keyof WithFractionOfFreeSpace = "freeSpaceInParent"

/**
 * @internal
 */
export function supportsFractionOfFreeSpace(obj: object): obj is object & WithFractionOfFreeSpace {
    return key in obj
}

/**
 * @internal
 */
export function freeSpaceFromProps(props: object): WithFractionOfFreeSpace | null {
    if (!supportsFractionOfFreeSpace(props)) return null
    return {
        freeSpaceInParent: props.freeSpaceInParent,
        freeSpaceUnitDivisor: props.freeSpaceUnitDivisor,
    }
}
